import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-book',
  templateUrl: './book.component.html',
  styleUrls: ['./book.component.css']
})
export class BookComponent implements OnInit {

  sonPuan = 0;
  yeniYorum = '';

  kitap = {adi : 'Atatürk',
          aciklama:'Çağdaş Türkiye nasıl kuruldu!',
          puan:4,
          puanDetay : [
            {yildiz : 5,puan:80},
            {yildiz : 4,puan:55},
            {yildiz : 3,puan:50},
            {yildiz : 2,puan:40},
            {yildiz : 1,puan:15},],
            kisiSayisi:125014,
            yorumlar:[{
              yildiz:5,
              yorum:'Kitap mükemmel'
            }]};

  constructor() { }

  yorumEkle(){
    this.kitap.yorumlar.push({yildiz:this.sonPuan,yorum:this.yeniYorum});
    console.log(this.kitap.yorumlar);
    this.kitap.kisiSayisi++;
  }

  puanla(puan: number) {
    // puan artışını göstermek için böyle bir kural belirledim.
    this.kitap.puan = ((this.kitap.puan * 10) + puan) / 10;
    this.kitap.puanDetay.map(y => {
      if (y.yildiz === puan) {
        console.log(y.puan);
        y.puan += puan;
        return y.puan;
      }
    });
    

    this.kitap.kisiSayisi++;
    this.sonPuan = puan;
  }

  ngOnInit() {
  }

}
